@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,700");
.App {
  text-align: center;
  background-image: linear-gradient(to bottom right, #9fb8ad 0%, #1fc8db 75%, #2cb5e8 95%);
  min-height: 500px;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.App-logo {
  height: 20vmin;
  margin-bottom: 15px;
  border-radius: 20%;
  border-style: solid;
  border-color: #e4e3e3;
  border-width: 3px; }

.App-header {
  color: white; }
  .App-header__icon {
    position: relative;
    top: 5px; }

.App-link {
  color: #61dafb; }

.btn {
  color: white;
  text-decoration: none;
  display: block;
  font-family: sans-serif;
  margin-bottom: 10px;
  padding: 7px; }
  .btn:hover {
    background-color: #5688c2; }

.contact {
  background-color: gray; }

div.content {
  position: relative;
  width: 100%;
  padding-top: 20px; }
  div.content h1 {
    margin-top: 0; }

#home-content {
  color: white;
  position: relative;
  background-color: #dddddd;
  padding: 40px;
  min-height: 50vh;
  color: #5c5c5c; }
  #home-content > div {
    background-color: white;
    padding: 20px;
    height: 100%; }

#projects-content {
  color: white;
  position: relative;
  background-color: rgba(23, 39, 126, 0.664);
  padding: 25px;
  min-height: 50vh;
  box-sizing: border-box; }
  #projects-content > div {
    background-color: white;
    padding: 20px;
    height: 100%; }

#contact-content {
  color: white;
  position: relative;
  background-color: #dddddd;
  padding: 25px;
  min-height: 15vh;
  color: #1a3057;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  #contact-content > div {
    background-color: white;
    padding: 20px;
    height: 100%; }
  #contact-content > h1 {
    margin-top: 0; }
  #contact-content > * {
    position: relative;
    bottom: 20px; }

.contact-links > a {
  text-decoration: none; }
  .contact-links > a > i {
    margin: 20px;
    color: #1a3057;
    font-size: 40px; }
    .contact-links > a > i:hover {
      color: gray; }

.projects-row {
  display: flex; }
  .projects-row > a {
    display: block;
    margin: 40px;
    flex-basis: 30%;
    flex-grow: 1;
    text-decoration: none; }

.project-entry {
  min-width: 30%;
  min-height: 30%;
  background-color: white;
  color: black;
  padding: 20px;
  border-style: solid;
  border-width: 1px;
  border-color: black;
  border-radius: 3px;
  font-size: 14px;
  font-family: sans-serif; }
